'use strict';

// MODULES //

import base from '@stdlib/math-base-special-abs';
import strided from '@stdlib/math-strided-special-abs';
import dispatcher from '@stdlib/ndarray-dispatch';
import unary from '@stdlib/ndarray-base-unary';
import types from './types.json';
import meta from './meta.json';
import data from './data.js';


// MAIN //

var table = {
	'number': base,
	'complex': null,
	'array': strided,
	'ndarray': dispatcher( unary, types, data, meta.nargs, meta.nin, meta.nout )
};


// EXPORTS //

export default table;
